@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@300&display=swap);
* {
  font-family: "Noto Sans", sans-serif;
}
.App {
  font-family: sans-serif;
  text-align: center;
}
input {
  width: 80%;
  margin: 2vw auto;
}

.container {
  width: 99vw;
  height: 95vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

.bookcontainer {
  width: 90%;
  flex-direction: column;
  display: flex;
  height: 200%;
  padding: 2vw 0vw;
  /* margin: auto; */
}

.books {
  display: inline-block;
  font-size: 1vw;
  width: 100%;
  /* background: #ffd1d140; */
  margin: 0.1vw 5vw;
  /* text-align: center; */
  /* border-radius: 2vw; */
  padding: 0.5vw 0.8vw;
  box-shadow: 0px 1px 2px 0px;
  /* border: 1px solid; */
  transition: all 0.4s ease-in-out;
}
.books h2 {
  width: 30%;
}
.books h3 {
  width: 30%;
  text-align: center;
}
.books p {
  width: 30%;
  text-align: end;
  text-decoration: underline;
}
.books div {
  display: flex;
  justify-content: space-around;
}
.books:hover {
  box-shadow: 2px 2px 5px 0px;
}

